
import Cell from 'vux/src/components/cell/index.vue'

export default {
props:['loginname'],
components:{
Cell
},
data:function () {
return{
arr:[]
}
},
beforeRouteEnter:function (to ,from ,next) {
next(function (vm) {
vm.$http.get('/topic_collect/'+vm.loginname).then(function (res) {
console.log(res.data.data)
vm.arr=res.data.data
})
})
}
}



