
import {mapGetters,mapMutations} from 'vuex'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

export default {
name: 'app',
data:function(){
return{
badgeText:''
}
},
components: {

ButtonTab, ButtonTabItem ,Badge,

XHeader,
Actionsheet
},

mounted(){   //绑定后的生命周期钩子函数
var loginStatus = window.localStorage.getItem('loginStatus')
if(loginStatus){   //登录状态
this.mutationIsLogined();
this.mutationLoginname(localStorage.getItem('loginname'))
this.mutationAccesstoken(localStorage.getItem('accesstoken'))
this.mutationAuthorId(localStorage.getItem('author_id'))


}
if(this.$store.state.isLogined){
this.getUnreadMsgCount();
var app =this;
window.setInterval(function(){
this.getUnreadMsgCount
},1000)
}
},
computed:{

...mapGetters(['moreMenus']),
isShowMoreMenus:{ //每个变量都有get和set属性
get:function(){ //需要获取时，自动触发get
return this.$store.state.isShowMoreMenus
},
set:function(nval){
this.$store.commit('mutationIsShowMoreMenus',nval)
}
}
},
methods:{
...mapMutations(['mutationIsLogined','mutationLoginname','mutationAccesstoken','mutationAuthorId']),


showMoreMenus(){
this.isShowMoreMenus=true
},

routeToHome(){
this.$router.push('/');
},
toMenu(menuKey, menuItem){

console.log(menuItem)
this.$router.push('/'+menuKey);
},

getUnreadMsgCount:function(){
var app= this;
this.$http.get('/message/count',{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(res){

app.badgeText=res.data.data
})
}
}
}



