
import xiangqin from './xiangqin.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
props:['id'],
components:{
Card,Cell,Group,xiangqin
},
data:function(){
return {
userInfo:null,
collect:[]

}
},
beforeRouteEnter:function(to,from,next){
next(function (vm) {
vm.$http.get('/user/'+vm.id).then(function (res) {
console.log(res.data.data)
vm.userInfo=res.data.data
})
vm.$http.get('/topic_collect/'+vm.id).then(function (res) {

vm.collect=res.data.data
})
})
},
beforeRouteUpdate:function(to,from,next){
var that =this
this.$http.get('/user/'+to.params.id).then(function (res) {
that.userInfo=res.data.data
})
this.$http.get('/topic_collect/'+to.params.id).then(function (res) {
this.collect=res.data.data
})
next()
},
methods:{
dianji:function () {
console.log(1)
this.$router.push('/xiangqing/'+this.userInfo.loginname)
}
}

}



