
import {mapState} from 'vuex'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
props:['name'],
computed:{
...mapState(['topics','isShowMoreMenus']),
},
data:function(){
return {
lists:[],

pullupconfig:{
content:'上拉加载更多',
downContent:'松开后进行加载',
upContent:'加载更多',
loadingContent:'加载中...'
},
pulldownconfig:{
content:'上拉进行刷新',
downContent:'松开后进行刷新',
upContent:'刷新',
loadingContent:'加载中...'
},
page:1,//当前页数
limit:6,//每页显示的数量
}
},
components:{
Cell,
Group,
Scroller,
Tab,
TabItem,
},
methods:{

routeTo(index){

this.$router.push('/topiclist/'+this.topics[index].name)
},
loadMoreDate(){

this.page=this.page+1;
var app=this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){
console.log(res.data.data)
app.lists=app.lists.concat(res.data.data)


app.$refs.pull.donePullup(); 
})

},
upDate(){
var that=this
this.page=1
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(res){
console.log(res.data)
that.lists=res.data.data
that.$refs.pull.donePulldown(); 
})
}
},

beforeRouteEnter:function(to,from,next){

next(function(vm){
vm.$http.get('/topics',{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(res){
console.log(res.data)
vm.lists=res.data.data
})
})
},
beforeRouteUpdate:function(to,from,next){
var that=this
this.$http.get('/topics',{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function(res){
console.log(res.data)
that.lists=res.data.data
})
next();
}
}



